import C from '~/Constants';
import T from 'Toolbox';
import platform from 'platform';
import React from 'react';
import MooReact from '~/MooReact';
import Sel from 'Sel';
import tracker from '~/tracker';
import FancyContenteditable from 'web/Components/FancyContenteditable';
var ReactNoteEditor = MooReact.createClass({
  displayName: 'ReactNoteEditor',
  componentDisplay: C.ComponentDisplay.Everywhere,
  // propTypes:
  // {
  //     item: React.PropTypes.object.isRequired,
  //     selectionStart: React.PropTypes.number,
  //     selectionEnd: React.PropTypes.number
  // },
  init: function init() {
    var props = this.props,
        note = props.item.hasNote() && props.item.getNote(),
        value = note ? note.getParsedText() : undefined;
    this.createObservables({
      hasValue: !!value
    });
  },
  onUnmount: function onUnmount() {
    clearTimeout(this._noteUpdateTimeout);
    this._noteUpdateTimeout = undefined;

    this._saveNote();

    if (platform.isMobile) {
      Sel.closeKeyboard();
    }
  },
  getValue: function getValue() {
    return this.refs.noteText.getValue();
  },
  _saveNote: function _saveNote() {
    tracker.beginAction();
    T.PaneUpdateBatcher.beginUpdateItems();
    var item = this.props.item,
        note = item.hasNote() && item.getNote();
    var noteValue = this.refs.noteText.getValue();

    if (!note && noteValue) {
      // Add note
      note = item.createNote();
    }

    if (note) {
      if (noteValue !== note.getRawText()) {
        note.setText(noteValue, C.ChangeType.Local);
      }

      if (!noteValue) {
        item.setNote(undefined, C.ChangeType.Local);
      }
    }

    T.PaneUpdateBatcher.commitUpdateItems();
    tracker.endAction();
  },
  onChange: function onChange() {
    var hasValue = this.refs.noteText.getValue().length > 0;

    if (hasValue !== this.hasValue.get()) {
      this.hasValue.set(hasValue); // Callback onValueSet after react has re-rendered

      if (hasValue && this.props.onValueSet) {
        requestAnimationFrame(this.props.onValueSet);
      }
    }

    clearTimeout(this._noteUpdateTimeout);
    this._noteUpdateTimeout = setTimeout(this._saveNote, 8000);
  },
  render: function render() {
    var props = this.props,
        fontSize = T.DisplaySettings.fontSize(),
        note = props.item.hasNote() && props.item.getNote(),
        value = note ? note.getParsedText() : undefined,
        placeholder = !note ? 'Add note' : undefined,
        height = this.state.hasValue ? props.heightValue : props.heightEmpty,
        isReadOnly = !props.item.canHaveNote();
    return React.createElement(FancyContenteditable, {
      ref: "noteText",
      className: props.className,
      value: value,
      placeholder: placeholder,
      focused: props.focused,
      onChange: this.onChange,
      onKeyDown: props.onKeyDown,
      onKeyPress: props.onKeyPress,
      selectionStart: props.selectionStart,
      selectionEnd: props.selectionEnd,
      fontSize: fontSize,
      margin: 0,
      height: height,
      tabIndex: props.tabIndex,
      richText: !!props.richText && !!props.goFullscreen,
      goFullscreen: props.richText && props.goFullscreen,
      onClickClose: props.onClickClose,
      isReadOnly: isReadOnly
    });
  }
});
export default ReactNoteEditor;